import Vue from 'vue'
import App from './App.vue'
import router from './router'
import firebase from 'firebase'


import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.css';


Vue.config.productionTip = false;

let app = '';
 // Initialize Firebase
  const config = {
    apiKey: "AIzaSyA3F-YmEURVF38HVdJp9TLtjDYai8oXYlg",
    authDomain: "mynewlicense.firebaseapp.com",
    databaseURL: "https://mynewlicense.firebaseio.com",
    projectId: "mynewlicense",
    storageBucket: "mynewlicense.appspot.com",
    messagingSenderId: "1077556037006"
  };
  firebase.initializeApp(config);

  Vue.use(VueMaterial)

  firebase.auth().onAuthStateChanged(() => {
    if (!app) {
      /* eslint-disable no-new */
      app = new Vue({
        router,
        render: h => h(App)
      }).$mount('#app');
     


    }
});

