<template>
  <div class="sign-up">
       <h4>Let's create your account and  profile !</h4>
       

      <div class="card-profile">
          <form id="form" @submit="signUp" class="form-profile">

             
             
                <input type="text" v-model="fname" placeholder="First Name"><br>
                <input type="text" v-model="lname" placeholder="Last Name"><br>
                <input type="datetime" v-model="dofb" placeholder="Date of Birth"><br>
               
                <label for ="gender"> Gender </label>
                <select id = "gender" name="gender" >
                         <option value = "male"> Male </option>
                         <option value = "female">Female </option>
                </select>

                  <label>Browser Select</label>
  <select class="browser-default">
    <option value="" disabled selected>Choose your option</option>
    <option value="1">Option 1</option>
    <option value="2">Option 2</option>
    <option value="3">Option 3</option>
  </select>
  
              

               <input type="text" v-model="email" placeholder="Email"><br>
               <input type="password" v-model="password" placeholder="Password"><br>

               <p> Set a Validation Question </p>
              
               <br>
               <br>

              <input type="submit" value="Submit" class="btn black white-text">
            </form>
      </div>

        <p>or go back to <router-link to="/login">login</router-link>. </p>
  </div>
   
</template>

 <script>
  import firebase from 'firebase';
  export default {
    name: 'userProfile',
    data() {
      return {
        email: '',
        password: ''
      }
    },
    methods: {
      signUp: function() {
        firebase.auth().createUserWithEmailAndPassword(this.email, this.password).then(
          (user) => {
          //  this.$router.replace('home')
            this.$router.push({ name: "home" });
            alert('New Account was created succesfully');
          },
          (err) => {
            alert('Oops. ' + err.message)
          }
        );
      }
    }


    
  }
</script>

 <style scoped>

 h4 {
     font-size: 1.7rem;
