<template>
 <div class="col s12 m6 login-form">
           <!--  <img src="../assets/logo.jpg" alt = "logo" > -->
         <div class="card-panel green darken-4">
            <p class="white-text">Log In</p>

            <form id="form"  @submit="login">
             
              <div class="input-field">
                 <input id="email" type="email"  v-model="email" class="validate black-text" placeholder="Enter Email">
              
              </div>


               <div class="input-field col s12">
                    <input id="password" type="password" v-model="password" placeholder="Enter Password" class="black-text">
                </div>
             
              <input type="submit" value="Log In" class="btn-login">
            </form>
          </div>

           <div class="row">
              
                   <a class="text orange-text social-button" href="" v-on:click="socialLogin"> <span>     <img src="../assets/google-logo.png" alt = "google logo" class="social-button img" >
                       </span> Google Account</a>
            </div>
            <div class="row">
                   <a class="text red-text" href="" v-on:click="onClickForgotPass">Forgot password?</a>
            </div>
            <div class="row">
                       <a  class="black-text" href="" v-on:click="onClickCreateAccount">Create an Account</a>
            </div>
        </div>

</template>

<script>
import firebase from 'firebase';

export default {
  name: "LoginForm",
  components: {},
  data() {
    return {
      email: "",
      password: ""
    };
  },
  methods: {
    onClickLogin(e) {
      e.preventDefault();

      const loginUser = {
        email: this.email,
        password: this.password
      };

      
       if(this.email ==='admin@gmail.com' || this.password ==='password'){
            this.$router.push({name:'adminSection'})
       }else{
           this.$router.push({name:'userProfile'})
       }
       

      // Send up to parent
      // this.$emit('add-todo', newTodo);

      this.email = "";
      this.password = "";
    },

    onClickForgotPass(e) {
      this.$router.push({ name: "forgotPass" });
      e.preventDefault();
    },

   
    onClickCreateAccount: function(e) {
      this.$router.push({ name: "register" });

      e.preventDefault();
    },

    login() {

        alert('Estoy en el login');
        firebase.auth().signInWithEmailAndPassword(this.email, this.password).then((user) => {
          this.$router.replace('home');
        }).catch((err) => {
          alert('Oops. ' + err.message)
        });
      },
      socialLogin() {
        const provider = new firebase.auth.GoogleAuthProvider();
        firebase.auth().signInWithPopup(provider).then((result) => {
          this.$router.replace('home');
        }).catch((err) => {
          alert('Oops. ' + err.message)
        });
      }
    }
  
  }

</script>

<style scoped>

.login-form {
    position: absolute;
    left: 0px;
    margin-top: 0px;
    margin-left:70% ;
    background-color: transparent;
    opacity: 1;
   
  }
  input {
    margin: 10px 0; 
    padding: 10px;
  }
  button {
    margin-top: 20px;
    width: 10%;
    cursor: pointer;
  }
  p {
    margin-top: 0px;
    font-size: 13px;
  }
  p a {
    text-decoration: underline;
    cursor: pointer;
  }
  .social-button {
    width: 75px;
    background-color: white;
    color: black;
    padding: 10px;
    outline: 0;
    border: 0;
    height: 75px;
  }

  .social-button img {
    background-color: transparent;
    border: none;
  }
  .btn-login{
      min-width:5vw;
      max-height:40px;
      border-radius: 10px;
  }

  
 @media (max-width: 600px) {
 .login-form {
    position: absolute;
    left: 4.2%;
    margin-top: 0px;
    margin-left:0% ;
    background-color: white;
    opacity: 1;
    border: 1px solid whitesmoke;
    box-shadow: 4px 4px 4px gray;
     padding-top:0px;
     min-width: 80vw;

  }
  input {
    margin: 10px 0; 
    padding: 10px;
  }
  button {
    margin-top: 20px;
    width: 10%;
    cursor: pointer;
  }
  p {
    margin-top: 0px;
    font-size: 13px;
  }
  p a {
    text-decoration: underline;
    cursor: pointer;
  }
  .social-button {
    width: 75px;
    background-color: white;
    color: black;
    padding: 10px;
    outline: 0;
    border: 0;
    height: 75px;
  }

  .social-button img {
    background-color: transparent;
    border: none;
  }
  .btn-login{
      min-width:5vw;
      max-height:40px;
      border-radius: 10px;
  }


  
}
</style>





